.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-wrapper-absolute {
		top: unset;
		bottom: 0;
		left: 50%;
		width: 80%;
		transform: translateX(-50%);
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		background: $stroke-gradient;
		overflow: hidden;
		mask-image: url(../images/mask-substract-3.png);
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		&::before {
			position: absolute;
			top: 2px;
			right: 2px;
			bottom: 2px;
			left: 2px;
			background: var(--color-main);
			mask-image: url(../images/mask-substract-3.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			content: "";
		}
		@include max-md {
			padding: 2rem 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
