.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: $color-custom;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		background: var(--color-gradient);
		border-radius: var(--border-radius);
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.15);
		padding: 2rem;
		overflow: hidden;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 10px;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-image: url(../images/border-substract.png);
					background-size: 100% 100%;
					background-repeat: no-repeat;
					content: "";
				}
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					mask-image: url(../images/mask-substract.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 3rem;
			color: $color-custom;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.15rem 0.2rem;
			border-radius: 5px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
