.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-wrapper-absolute {
		top: unset;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translateX(-50%);
		.background-element {
			img {
				object-fit: contain;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background: var(--color-gradient);
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
