.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 2px;
			background: $stroke-gradient;
			mask-image: url(../images/mask-substract.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask-image: url(../images/mask-substract.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		--mask: radial-gradient(40px at 40px 40px, #0000 98%, #000) -40px -40px;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			background: $stroke-gradient;
			mask: var(--mask);
			@include max-md {
				padding: 2rem 1rem 5rem;
			}
			&::before {
				position: absolute;
				top: 2px;
				right: 2px;
				bottom: 2px;
				left: 2px;
				background-image: url(../images/bg-venue-card.png);
				background-size: 100%;
				background-position: 50% 80%;
				background-repeat: no-repeat;
				background-origin: content-box;
				mask: var(--mask);
				content: "";
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
